import './App.css';
import { useEffect, useState } from 'react';
import { ConfigProvider, theme, Form, Input, Table, DatePicker, Select, Typography, message } from 'antd';
import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';
import 'dayjs/locale/es';
const { Title } = Typography;
dayjs.extend(customParseFormat);
dayjs.locale('es')
const { Option } = Select;
const style = {marginBottom: 30, padding: 15, marginTop:20}
const weekFormat = 'DD/MM';
// const weekFormatCustom = 'DDMM'; // FORMATEO EXCEL PRUEBAS
const weekFormatCustom = 'DD/MM';
const customWeekStartEndFormat = (value) =>
`${dayjs(value).startOf('week').format(weekFormat)} - ${dayjs(value)
  .endOf('week')
  .format(weekFormat)}`;
      
// columnas que quiero mostrar
const columnas = ['nombre', 'lunes', 'martes', 'miercoles', 'jueves', 'viernes', 'sabado', 'domingo', 'total horas', 'discord id'];
function App() {
  const url = 'https://docs.google.com/spreadsheets/d/'
  // const ssid = '1NnZ_iVlFmnsLYfZ0H4do-gm_XnupGktmIY2anbYMXtg' // EXCEL PRUEBAS
  const ssid = '1oBijxRcMaAmsYSVB4Uke5GUFmiWKEsU2F3tdqSSRaX4'
  const query1 = `/gviz/tq?sheet=`
  
  let endpoint = `${url}${ssid}${query1}`;
  
  const [form] = Form.useForm();
  const [columns, setColumns] = useState();
  const [data, setData] = useState();
  const [dataFilter, setDataFilter] = useState(); 
  const [loading, setLoading] = useState(true); 
  
  const getData = async (semana) => {
      if(semana != undefined)
        endpoint += semana
      await fetch (endpoint)
      .then(res => res.text())
      .then(data => {
        const temp = data.substr(47).slice(0, -2);
        const json = JSON.parse(temp);
        const cols = [];
        const rows = [];
        json.table.cols.shift();
        cols.push(({title: 'Nombre', dataIndex: 'A', key: 'A'})) // COLUMNA DE RANGO
        json.table.cols
          .filter((col) => col.label != "" && columnas.includes(col.label.toLowerCase().trim()))
          .map((c) => cols.push({title: c.label, dataIndex: c.id, key: c.id}));
        let ids = 0
        json.table.rows.map((r) => {
          // console.log(r.c)
          if(r.c[9] == null || r.c[0] == null)
            return;
            rows.push({
            key: ids,
            A: r.c[0].v, // Nombre
            B: r.c[1] === null ? "" : r.c[1].f, // Viernes
            C: r.c[2] === null ? "" : r.c[2].f, // Sabado
            D: r.c[3] === null ? "" : r.c[3].f, // Domingo
            E: r.c[4] === null ? "" : r.c[4].f, // Lunes
            F: r.c[5] === null ? "" : r.c[5].f, // Martes
            G: r.c[6] === null ? "" : r.c[6].f, // Miercoles
            H: r.c[7] === null ? "" : r.c[7].f, // Jueves
            J: r.c[9] === null ? "" : r.c[9].f, // Total
            O: r.c[14] === null ? "" : r.c[14].v, // Discord ID
            })
            ids++;
        })
        setColumns(cols.slice(0,12))
        setData(rows)
        setDataFilter(rows)
        setLoading(false)
        })
    } 
  
  useEffect(() => {
    getData()
  }, []);

  const filterDiscordID = (did) => {
    did = did.trim()
    const result = data.filter((d) => d.O == did)
    if (did == "")
      setDataFilter(data)
    else if(result.length > 0)
      setDataFilter(result)
    else
      setDataFilter(data)
  } 

  const filterNombre = (nombre) => {
    nombre = nombre.trim().toLowerCase()
    const result = data.filter((d) => d.A.toLowerCase().includes(nombre))
    if (nombre == "")
      setDataFilter(data)
    else if(result.length > 0)
      setDataFilter(result)
    else
      setDataFilter(data)
  }

  const semana = (semana) => {
    const mesActual = dayjs(semana).endOf('week').add(1, 'day').format('MMMM');
    const mesActualConMayuscula = mesActual.charAt(0).toUpperCase() + mesActual.slice(1);
    const namePage = `Semana ${dayjs(semana)
      .endOf('week').add(1, 'day')
      .format(weekFormatCustom)} ${mesActualConMayuscula}`;

    const test = 'Semana 26/01 Junio'
    getData(test)
  }

  return (
    <ConfigProvider     
      theme={{
        algorithm: theme.darkAlgorithm,
      }}>
    <div className="App">
      <Form form={form} layout="inline">
        <Form.Item
          style={style}
          label="Semana">
          <DatePicker defaultValue={dayjs()} format={customWeekStartEndFormat} onChange={(e) => semana(e)} picker="week" />
        </Form.Item>
        <Form.Item
          style={style}
          label="Nombre">
          <Input onChange={(e) => filterNombre(e.target.value)}/>
        </Form.Item>
        <Form.Item
          style={style}
          label="Discord ID">
          <Input onChange={(e) => filterDiscordID(e.target.value)}/>
        </Form.Item>
        {/* <Form.Item
          style={{marginBottom: 30, padding: 15, marginTop:20, width: 250, alignContent: 'start' }}
          label="Rango"
          size="large">
            <Select 
              options={rangos2}
            >
            
          </Select>
        </Form.Item> */}
      </Form>

      <Table 
        loading={loading}
        style={{padding: 10}}
        bordered={true}
        pagination={false}
        columns={columns}
        dataSource={dataFilter}
        scroll={{
          y: 625
        }}
        footer={() => 'Notificar si algún dato se encuentra desactualizado'}
        >
      </Table>
    </div>
    </ConfigProvider>
  );
}

export default App;
